<template>
  <div class="wrapper">
    <div class="start"></div>
    <div class="container">
      <div class="close"></div>
      <div class="box">
        <van-form @submit="onSubmit" class="form">
          <van-field
            class="selector"
            readonly
            clickable
            name="area"
            :value="userInfo.value"
            placeholder="请选择地区"
            @click="showArea = true"
          />
          <van-field
            class="inp"
            v-model="userInfo.username"
            :rules="[{ required: true, message: '请填写姓名' }]"
          />
          <van-field
            class="inp"
            v-model="userInfo.phone"
            type="text"
            name="手机号"
            :rules="[{ validator, message: '请填写手机号' }]"
          />
          <van-field
            class="inp"
            v-model="userInfo.address"
            type="text"
            name="密码"
            :rules="[{ required: true, message: '请填写详细地址' }]"
          />
          <van-field
            class="textbox"
            v-model="userInfo.message"
            rows="2"
            autosize
            type="textarea"
            maxlength="10"
            placeholder="请输入您的文字梦想："
            show-word-limit
          />
        </van-form>
        <van-popup v-model="showArea" position="bottom">
          <van-area
            :area-list="areaList"
            columns-num="2"
            @confirm="onConfirm"
            @cancel="showArea = false"
          />
        </van-popup>
      </div>
      <div class="radio" v-show="!isAgree" @click="onIsCheck"></div>
      <div class="radio1" v-show="isAgree" @click="onIsCheck"></div>
      <div class="confirm_text" @click="onIsShowMask"></div>
    </div>
    <button class="btn" @click="isMask"></button>
    <div class="bird1"></div>
    <div class="bird2"></div>
    <div class="mask" v-show="isShowMask">
      <div class="dream"></div>
      <div class="closeBtn" @click="onIsShowMask"></div>
      <div class="rules">
        <div class="treaty"></div>
      </div>
    </div>
    <div class="mask1" v-show="isMask2">
      <div class="hint"></div>
      <div class="backchange" @click="isMask2 = false"></div>
      <div class="confirmdream" @click="onSubmit"></div>
    </div>
  </div>
</template>

<script>
import { Button, Field, Form, Popup, Area, Toast } from "vant";
import { areaList } from "@vant/area-data";
export default {
  components: {
    [Button.name]: Button,
    [Form.name]: Form,
    [Field.name]: Field,
    [Area.name]: Area,
    [Popup.name]: Popup,
    [Toast.name]: Toast,
  },
  data() {
    return {
      userInfo: {
        username: "",
        phone: "",
        address: "",
        value: "",
        message: "",
      },
      areaList,
      showArea: false,
      areaList, // 数据格式见 Area 组件文档
      isAgree: false,
      isShowMask: false,
      isMask2: false,
    };
  },
  methods: {
    onSubmit() {
      localStorage.setItem("dream", JSON.stringify(this.userInfo));
      this.$router.push("/load");
    },
    onConfirm(values) {
      this.userInfo.value = values
        .filter((item) => !!item)
        .map((item) => item.name)
        .join(" ");
      this.showArea = false;
    },
    onIsCheck() {
      this.isAgree = !this.isAgree;
    },
    //正则校验手机号
    validator(val) {
      if (
        !/^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/.test(
          val
        )
      ) {
        Toast({
          duration: 1800,
          message: "您输入的手机号格式不正确",
        });
      } else {
        return true;
      }
    },
    onIsShowMask() {
      this.isShowMask = !this.isShowMask;
    },
    isMask() {
      const { username, phone, address, value, message } = this.userInfo;
      if (
        username === "" ||
        phone === "" ||
        address === "" ||
        value === "" ||
        message === ""
      ) {
        Toast({
          duration: 1800, // 持续展示 toast
          message: "请确保个人信息填写完毕",
        });
      } else {
        //判断是否勾选用户个人隐私保密协议
        if (!this.isAgree) {
          Toast({
            duration: 1800, // 持续展示 toast
            message: "请勾选用户隐私保密协议",
          });
        } else {
          this.isMask2 = true;
        }
      }
    },
  },
};
</script>

